





























































import { Component, Vue } from "vue-property-decorator";
import { mapActions , mapState } from "vuex";
import Advertisement from "@/components/adv.vue";

export default Vue.extend({
    data() {
        return {};
    },
    mounted() {
        this.get_data({ id: 1 });
        this.get_adve();
        this.get_chap({ id: this.info.id });
    },
    methods: {
        ...mapActions([
            "get_data",
            "get_adve",
            "get_chap",
        ]),
    },
    computed: {
        ...mapState({
            info: ( state: any ) => state.comic_info,
            adve: ( state: any ) => state.adver_info,
            chap: ( state: any ) => state.chapt_info,
        }),
    },
    components: {
        adv: Advertisement,
    },
});
